@charset "UTF-8";
@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

html {
  font-size: 62.5%;
}
@media only screen and (max-width: 78.125em) {
  html {
    font-size: 56.25%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 43.75%;
  }
}

body {
  box-sizing: border-box;
}

::selection {
  background-color: rgba(222, 162, 21, 0.8);
}

video {
  margin-top: 4rem;
  max-width: 50rem;
  max-height: 50rem;
}

body {
  font-family: "Lato", sans-serif;
  color: #222;
}

p,
li {
  list-style: none;
  font-size: 1.6rem;
  line-height: 1.4;
}
@media only screen and (max-width: 37.5em) {
  p,
  li {
    line-height: normal;
  }
}

.text-colored {
  background: linear-gradient(to right, #dea215, #ecb13b);
  -webkit-background-clip: text;
  color: transparent;
}

h1,
h2,
h3,
h4 {
  text-transform: uppercase;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}
@media only screen and (max-width: 56.25em) {
  h2 {
    font-size: 3rem;
  }
}
@media only screen and (max-width: 37.5em) {
  h2 {
    font-size: 2.5rem;
  }
}

h3 {
  font-size: 2rem;
  line-height: 1.3;
}
@media only screen and (max-width: 37.5em) {
  h3 {
    font-size: 1.7rem;
  }
}

h4 {
  font-size: 1.5rem;
}

.montserrat {
  font-family: "Montserrat Alternates", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

label {
  font-size: 1.4rem;
  color: #222;
}
@media only screen and (max-width: 37.5em) {
  label {
    font-size: 1.2rem;
  }
}

.container {
  max-width: 114rem;
  margin: 0 auto;
}
.container--nav {
  height: 100%;
  padding: 0 8rem;
}
@media only screen and (max-width: 37.5em) {
  .container--nav {
    padding: 0 5rem;
  }
}
@media only screen and (max-width: 78.125em) {
  .container {
    max-width: 94rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .container {
    max-width: 74rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .container {
    max-width: 54rem;
  }
}

.row-2-1 {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 3.5rem;
}
@media only screen and (max-width: 56.25em) {
  .row-2-1 {
    grid-template-columns: 1fr;
  }
}

.section {
  padding: 12rem 0;
}
.section--months {
  background-image: url(../src/img/sand.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.section--grey {
  padding: 22rem 0 12rem 0;
  background-color: #f8f8f8;
}

.u-text-center {
  text-align: center;
}

.u-margin-bottom-tiny {
  margin-bottom: 1rem !important;
}

.u-margin-bottom-small {
  margin-bottom: 2rem !important;
}

.u-margin-bottom-medium {
  margin-bottom: 4rem !important;
}

.u-margin-bottom-big {
  margin-bottom: 6rem !important;
}
@media only screen and (max-width: 56.25em) {
  .u-margin-bottom-big {
    margin-bottom: 5rem !important;
  }
}

.u-margin-bottom-huge {
  margin-bottom: 10rem !important;
}

.u-margin-top-huge {
  margin-top: 5rem !important;
}

.u-uppercase {
  text-transform: uppercase;
}

.color-white {
  color: #fff;
}

.u-negative-margin-top {
  margin-top: -25rem;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1.5rem 3rem;
  border-radius: 1.5rem;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.2rem 0.6rem 1rem rgba(34, 34, 34, 0.15);
}
.btn:hover, .btn:link:hover, .btn:visited:hover {
  transform: translateY(-2px);
  box-shadow: 0.2rem 1.2rem 1rem rgba(34, 34, 34, 0.15);
}
.btn--black {
  background-color: #222;
  color: #fff;
}
.btn--yellow {
  background: linear-gradient(to right, #dea215, #ecb13b);
  color: #222;
}
.btn--yellow:after {
  position: absolute;
  background: linear-gradient(to right, #dea215, #9e7937);
  opacity: 0;
  top: 0;
  left: 0;
  padding: 1.5rem 3rem;
  border-radius: 0.5rem;
  display: inline-flex;
  align-items: center;
  transition: all 0.3s;
}
.btn--book-now::after {
  content: "Book Now";
}
.btn--learn-more::after {
  content: "Learn More";
}
.btn--submit::after {
  content: "Изпрати";
}
.btn:hover:after {
  opacity: 1;
}
.btn:active {
  transform: scale(0.97);
}

.destination {
  position: relative;
  border-radius: 1.5rem;
  object-fit: cover;
  max-height: 24rem;
}
@media only screen and (max-width: 56.25em) {
  .destination {
    height: 24rem;
    width: 35rem;
  }
}
.destination:after {
  content: "";
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 1.5rem;
  transition: 0.5s;
  backdrop-filter: blur(1px);
  background-color: rgba(222, 162, 21, 0.25);
}
.destination:hover::after {
  opacity: 1;
}
.destination--item1 {
  grid-column: 1/3;
  grid-row: 1;
}
@media only screen and (max-width: 56.25em) {
  .destination--item1 {
    grid-column: unset;
    grid-row: unset;
  }
}
.destination--item2 {
  max-height: 55rem;
  grid-column: 3;
  grid-row: 1/3;
}
@media only screen and (max-width: 56.25em) {
  .destination--item2 {
    grid-row: unset;
    grid-column: unset;
  }
}
.destination:hover .destination__caption {
  opacity: 1;
  bottom: 2.5rem;
}
.destination__caption {
  opacity: 0;
  width: 50%;
  position: absolute;
  bottom: 0;
  left: 2rem;
  font-size: 2rem;
  color: #fff;
  font-weight: 600;
  z-index: 1;
  transition: 0.5s;
}
.destination__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 1.5rem;
}

.testimonial {
  background-color: #fff;
  box-shadow: 1px 1px 10px rgba(34, 34, 34, 0.15);
  padding: 3rem 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1.5rem;
  flex-shrink: 0;
  width: 30%;
  position: relative;
}
.testimonial:not(:first-child) {
  margin-left: 5rem;
}
@media only screen and (max-width: 56.25em) {
  .testimonial {
    width: 48%;
  }
}
@media only screen and (max-width: 37.5em) {
  .testimonial {
    width: 100%;
  }
}
.testimonial::before {
  content: "“";
  position: absolute;
  top: 0%;
  left: 3%;
  font-size: 16rem;
  line-height: 1;
  color: #dea215;
  opacity: 0.2;
}
.testimonial__img-box {
  height: 12rem;
  width: 12rem;
  border-radius: 50%;
  margin: 1.5rem;
  border: 5px solid rgba(222, 162, 21, 0.75);
}
.testimonial__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  border-radius: 50%;
}
.testimonial__name {
  font-size: 1.8rem;
  font-weight: 600;
}

.scene {
  height: 100%;
  perspective: 150rem;
  transition: all 0.7s;
  display: inline-block;
  width: auto;
}
@media only screen and (max-width: 78.125em) {
  .scene {
    width: 28rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .scene {
    width: 34rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .scene {
    width: 35rem;
  }
}
.scene:hover .card__img {
  transform: scale(1.1);
  transition: transform 0.7s;
}
@media only screen and (max-width: 56.25em), only screen and (hover: none) {
  .scene:hover .card__img {
    transform: none;
  }
}

.card {
  overflow: hidden;
  border-radius: 2rem;
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 0.5s;
  transform-style: preserve-3d;
  box-shadow: 0rem 1rem 1.5rem rgba(34, 34, 34, 0.15);
}
.card__bottom {
  padding: 2rem;
}
.card__img-box {
  height: 22rem;
  background-size: cover;
  background-blend-mode: screen;
  overflow: hidden;
}
.card__img {
  border-radius: 2rem 2rem 0 0;
  transition: 0.7s;
}
.card__title {
  font-size: 2rem;
  color: #222;
  font-weight: 400;
  padding: 0.5rem 1.5rem;
  transition: all 0.3s;
  text-align: center;
}
.card__price {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 90%;
  left: 50%;
  font-size: 3rem;
  font-weight: 400;
  color: #222;
  text-align: center;
  transition: all 0.3s;
}

.row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  gap: 5rem;
}
@media only screen and (max-width: 56.25em) {
  .row {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (max-width: 37.5em) {
  .row {
    grid-template-columns: repeat(1, 1fr);
    gap: 5rem;
  }
}

.arrow {
  cursor: pointer;
  font-size: 1.2rem;
  color: #222;
  height: 3rem;
  width: 3rem;
  display: inline-flex !important;
  justify-content: center;
  align-items: center;
  background-color: rgba(222, 162, 21, 0.8);
  border-radius: 50%;
  border: 1ps solid transparent;
  will-change: transform;
  color: #fff;
}
.arrow:not(:last-child) {
  margin-right: 0.5rem;
}
.arrow:hover {
  background-color: #fff;
  color: #222;
  border: 1px solid rgba(222, 162, 21, 0.8);
}
.arrow:active {
  transform: scale(0.98);
}

.icon {
  margin-right: 1rem;
  z-index: 0;
  font-size: 2rem;
  position: relative;
  transition: 0.3s;
  color: #222;
  padding: 1.5rem;
  width: 3rem;
  height: 3rem;
  display: flex !important;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}
.icon--circle {
  border: 0.2rem solid #dea215;
  cursor: default;
}
.icon--facebook::after {
  background-color: #2050b3;
}

.icon-social {
  cursor: pointer;
  font-size: 3rem;
  color: #747474;
  cursor: pointer;
  transition: 0.3s;
}
.icon-social:hover {
  color: #222;
}
@media only screen and (max-width: 56.25em) {
  .icon-social {
    display: none !important;
  }
}

.input {
  outline: none;
  padding: 1.3rem;
  border-radius: 0.5rem;
  border: 3px solid #e9e9e9;
  width: 100%;
}
.input:not(:last-child) {
  margin-bottom: 1.3rem;
}
.input::placeholder {
  font-size: 1.4rem;
}
.input:focus {
  border: 3px solid #dea215;
}

form {
  padding: 0 5rem;
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media only screen and (max-width: 56.25em) {
  form {
    padding: 0;
  }
}

textarea {
  height: 15rem;
}

.privacy-policy {
  display: inline;
}

.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  z-index: 99;
}
.modal__overlay {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(34, 34, 34, 0.8);
  z-index: -100;
  backdrop-filter: blur(10px);
}
.modal__content {
  padding: 4.5rem;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background-image: linear-gradient(rgba(222, 162, 21, 0.9), rgba(248, 248, 248, 0.9)), url(../src/img/beach.jpg);
  background-position: center;
  background-size: cover;
  transform: translate(-50%, -50%) scale(0.5);
  transition: all 0.4s 0.2s;
  border-radius: 3rem;
}
@media only screen and (max-width: 56.25em) {
  .modal__content {
    width: 50rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .modal__content {
    width: 45rem;
  }
}
.modal__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-width: 75%;
}
@media (max-width: 56.25em) {
  .modal__text {
    max-width: 100%;
  }
}
.modal__close {
  font-size: 3.6rem;
  text-decoration: none;
  position: absolute;
  top: 0.2rem;
  right: 1.6rem;
  color: #f8f8f8;
  cursor: pointer;
}
.modal__close:hover {
  color: #9e7937;
}
.modal:target {
  opacity: 1;
  visibility: visible;
}
.modal:target .modal__content {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.visible {
  opacity: 1;
  visibility: visible;
}
.visible--content {
  transform: translate(-50%, -50%) scale(1);
}

.composition {
  display: inline-flex;
  justify-content: center;
  padding: 0 1rem;
}
.composition__img-box {
  height: 25rem;
  width: 20rem;
  position: relative;
  transition: 0.3s;
}
.composition__img-box:nth-child(even) {
  margin-top: 2rem;
}
.composition__img {
  width: 100%;
  height: 100%;
}
.composition__left {
  margin-right: 2rem;
  margin-top: 4rem;
}

.dropdown__content-responsive {
  display: none;
  border-radius: 0.5rem;
  min-width: 160px;
  z-index: 1;
  transition: all 3s;
}

.dropdown__content-responsive-2 {
  display: none;
  border-radius: 0.5rem;
  min-width: 160px;
  z-index: 1;
  transition: all 3s;
}

.dropdown-list li {
  margin: 0.5rem 0;
  list-style: none;
}

.dropdown-list a {
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #222;
  margin: 5rem 0;
  transition: 0.2s;
}
.dropdown-list a:hover {
  color: #dea215;
}

.months {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 1.5rem;
  margin: 0 2rem;
  padding: 3rem 2rem;
  justify-content: space-between;
  border-radius: 5rem;
  background-color: rgba(255, 255, 255, 0.8);
}
@media only screen and (max-width: 78.125em) {
  .months {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media only screen and (max-width: 56.25em) {
  .months {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (max-width: 37.5em) {
  .months {
    grid-template-columns: repeat(2, 1fr);
  }
}
.months__month {
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  cursor: pointer;
  transition: 0.3s;
}
.months__month:hover {
  transform: translateY(-0.5rem) scale(1.09);
}
@media only screen and (max-width: 78.125em) {
  .months__month {
    padding: 2rem 0;
  }
}
@media only screen and (max-width: 37.5em) {
  .months__month {
    padding: 1rem 0;
  }
}
@media only screen and (max-width: 56.25em), only screen and (hover: none) {
  .months__month:hover {
    transform: none;
  }
}
.months__month__text {
  color: #222;
}
@media only screen and (max-width: 78.125em) {
  .months__month__text {
    padding: 1rem 0;
  }
}

.page-numbers {
  font-size: 1.6rem;
  padding: 0.5rem 1rem;
  background-color: #222;
  color: #fff;
  border: 1px solid #222;
  border-radius: 5px;
  margin: 0 0.3rem;
  display: inline-flex;
}

.current {
  background-color: #fff;
  color: #222;
}

.header {
  background-image: url(../src/img/egypt.jpg);
  background-position: top;
  background-size: cover;
  height: 85vh;
  border-radius: 3rem;
  position: relative;
  margin: 10rem 3rem 0 3rem;
}
.header__text-box {
  text-align: center;
  display: flex;
  justify-content: center;
  height: 100%;
}
.header__text-box__title {
  animation: moveInLeft 2s ease-out;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  font-size: 4.5rem;
  position: absolute;
  top: 40%;
  color: #fff;
  text-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
}
@media only screen and (max-width: 37.5em) {
  .header__text-box__title {
    font-size: 3.7rem;
  }
}

.dropdown {
  position: relative;
  display: inline-block;
  transition: all 3s;
}
.dropdown__link {
  color: #222;
  transition: 0.3s;
}
.dropdown__link:hover {
  color: #dea215;
}
.dropdown:hover .dropdown__content {
  display: block;
}
@media only screen and (max-width: 56.25em) {
  .dropdown:hover .dropdown__content {
    display: none;
  }
}
.dropdown__content {
  display: none;
  position: absolute;
  background-color: #f8f8f8;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
  transition: all 3s;
  border-radius: 0.5rem;
}

.cat-item {
  margin: 0.5rem 0;
}

.cat-item a {
  color: #222;
  transition: all 0.3s;
}

.cat-item a:hover {
  color: #dea215;
}

@media only screen and (max-width: 56.25em) {
  .menu-header-menu-container {
    display: none;
  }
}

.footer {
  padding: 0 3rem;
  background: #222;
}
.footer__copyrights {
  color: #f8f8f8;
  font-size: 1.3rem;
  padding: 3rem 0;
}

.menu-footer-menu-container {
  border-bottom: 1px solid #f8f8f8;
}
@media only screen and (max-width: 37.5em) {
  .menu-footer-menu-container {
    grid-template-columns: none;
    gap: 2.5rem;
    justify-items: center;
    justify-content: center;
  }
}

.menu-footer-menu-container ul li a {
  line-height: 1.8;
  font-size: 1.2rem;
  color: #f8f8f8;
}

#menu-footer-menu {
  padding: 4rem 0;
}
@media only screen and (max-width: 78.125em) {
  #menu-footer-menu {
    padding: 3rem 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}

.nav {
  z-index: 10;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  height: 10rem;
  transition: 0.3s;
  background-color: #fff;
}
.nav__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.nav__logo__img {
  height: 6.5rem;
}

.sticky {
  height: 10rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 10;
}

.sticky ul li a {
  color: #222;
}

.sticky .icon-social {
  color: #747474;
}

.sticky .icon-social:hover {
  color: #222;
}

.sticky .hidden-cross {
  color: #222;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-item a {
  text-transform: uppercase;
  transition: 0.3s;
  font-weight: 500;
  color: #222;
}
.menu-item a:hover {
  color: #dea215;
}

.menu-header-menu li a {
  font-size: 1.6rem;
  margin: 10rem 0;
}
@media only screen and (max-width: 37.5em) {
  .menu-header-menu li a {
    font-size: 2rem;
  }
}

.menu-header-menu-container .menu .menu-item:not(:last-child) {
  padding-right: 2.7rem;
}

.hidden-cross {
  display: none;
  font-size: 3rem;
  color: #222;
  cursor: pointer;
}
@media only screen and (max-width: 56.25em) {
  .hidden-cross {
    display: block;
  }
}

.sidebar {
  backdrop-filter: blur(1rem);
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  height: 100vh;
  z-index: 11;
  width: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: -1rem 0 1rem rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 37.5em) {
  .sidebar {
    width: 100%;
  }
}

.sidebar li,
.sidebar li a {
  width: 100%;
  padding-bottom: 1.5rem;
}

.sidebar-menu-list {
  padding: 2rem 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.sidebar-menu-list li a {
  color: #222;
}

.icon-x {
  transition: 0.2s;
  padding: 2.5rem;
  font-size: 4rem;
}

.destinations__grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 2rem;
}
@media only screen and (max-width: 56.25em) {
  .destinations__grid {
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}
@media only screen and (max-width: 37.5em) {
  .destinations__grid {
    grid-template-columns: 1fr;
  }
}

.testimonials {
  background-color: #f8f8f8;
}
.testimonials__box {
  position: relative;
  overflow: hidden;
  padding: 5rem 2rem 2rem;
}
.testimonials__row {
  display: flex;
  left: -34.5%;
  position: relative;
}
@media only screen and (max-width: 56.25em) {
  .testimonials__row {
    left: -57%;
  }
}
@media only screen and (max-width: 37.5em) {
  .testimonials__row {
    left: 0;
  }
}
.testimonials__arrows {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 5%;
  left: 94.5%;
  display: inline-flex;
}
@media only screen and (max-width: 37.5em) {
  .testimonials__arrows {
    left: 90%;
  }
}

.holidays {
  background-image: linear-gradient(to right, rgba(222, 162, 21, 0.75), rgba(236, 177, 59, 0.75)), url(../img/boat.jpg);
  background-position: center;
  background-size: cover;
}
.holidays__box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
@media only screen and (max-width: 56.25em) {
  .holidays__box {
    grid-template-columns: repeat(2, 1fr);
  }
}
.holidays__holiday {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  transition: 0.3s;
  margin-bottom: 3rem;
}
.holidays__holiday:hover {
  transform: translateY(-1rem) scale(1.03);
}
@media only screen and (max-width: 56.25em), only screen and (hover: none) {
  .holidays__holiday:hover {
    transform: none;
  }
}
.holidays__icon {
  font-size: 6rem;
  width: 15rem;
  height: 15rem;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  border: 0.4rem solid #fff;
  border-radius: 50%;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  gap: 3rem;
}
@media only screen and (max-width: 56.25em) {
  .cards {
    gap: 8rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.page-content__info-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  border-radius: 3rem;
}

.page-content {
  background-color: #fff;
  padding: 3rem;
  border-radius: 3rem;
  box-shadow: 0rem 1rem 1.5rem rgba(34, 34, 34, 0.15);
}

.price {
  font-size: 2rem;
}

.single-content__img {
  height: 100%;
  transition: transform 0.7s;
  overflow: hidden;
  object-fit: cover;
  width: 100%;
  border-radius: 3rem;
}
.single-content__img:hover {
  transform: scale(1.1);
  transition: transform 0.7s;
}
@media only screen and (max-width: 56.25em), only screen and (hover: none) {
  .single-content__img:hover {
    transform: none;
  }
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}